const coverSection = document.querySelector(".covers-section"), options = { root: null, rootMargin: "500px", threshold: .5 }, observeFunc = o => { o.forEach((o => { o.isIntersecting ? startAnimation() : stopAnimation() })) }; function stopAnimation() { document.querySelectorAll(".covers-slide").forEach((o => o.classList.remove("animation"))) } function startAnimation() { document.querySelectorAll(".covers-slide").forEach((o => o.classList.add("animation"))) } const observer = new IntersectionObserver(observeFunc, options); observer.observe(coverSection);

