import Swiper from "swiper"; import "swiper/css/bundle"; import { Navigation, Keyboard, Mousewheel } from "swiper/modules"; function doSwipe() { new Swiper(".swiper", { modules: [Navigation, Keyboard, Mousewheel], keyboard: { enabled: !0 }, mousewheel: { enabled: !0 }, loop: !1, slidesPerView: 1, speed: 1e3, navigation: { prevEl: ".swiper-prev", nextEl: ".swiper-next", disabledClass: "disabled-button" } }) } document.addEventListener("DOMContentLoaded", doSwipe);
