function scrollFunction() { document.body.scrollTop > 200 || document.documentElement.scrollTop > 200 ? document.getElementById("scrollToTopBtn").style.display = "flex" : document.getElementById("scrollToTopBtn").style.display = "none" } window.onscroll = function () { scrollFunction() }, document.getElementById("scrollToTopBtn").onclick = function () { document.body.scrollTop = 0, document.documentElement.scrollTop = 0 };

